import { Component, OnInit, Input } from '@angular/core';
import { MaterialAngularModule } from '../material-angular/material-angular.module';
import { CommonModule } from '@angular/common';
import { NgxGalleryOptions, NgxGalleryImage, NgxGalleryAnimation } from 'ngx-gallery';

declare var $: any;
@Component({
  selector: 'pxt-gallery',
  templateUrl: './pxt-gallery.component.html',
  styleUrls: ['./pxt-gallery.component.css']
})
export class PxtGalleryComponent implements OnInit {

  galleryOptions: NgxGalleryOptions[];
  @Input() galleryImages: NgxGalleryImage[];
  @Input() width: any = "100%";
  @Input() height: any = '400px';
  constructor() {
  }
  ngOnInit() {

    this.galleryOptions = [
      {
        width: this.width,
        height: this.height,
        thumbnailsColumns: 4,
        imageAnimation: NgxGalleryAnimation.Slide
      },
      // max-width 800
      {
        breakpoint: 800,
        width: this.width,
        height: this.height,
        imagePercent: 80,
        thumbnailsPercent: 20,
        thumbnailsMargin: 20,
        thumbnailMargin: 20
      },
      {
        breakpoint: 400,
        preview: false
      }
    ];
  }
}
