import { MediaMatcher } from '@angular/cdk/layout';
import { ChangeDetectorRef, Component, OnDestroy, Input, ViewChild, ComponentFactoryResolver, Inject, ViewContainerRef } from '@angular/core';
import { PxtContentBody } from '../../directives/pxt-content-body';
import { Routes } from '@angular/router';
import { PxtApp } from './pxt-app';
import { PxtAppModel } from './model/pxt-app.model';
import { PxtAppComponentService } from '../../services/pxt-app-components.service';
import { AdComponent } from '../../interfaces/ad.component';
import { MatMenu, MatMenuTrigger } from '@angular/material';
import { group } from '@angular/animations';
import { SafeHtml, DomSanitizer } from '@angular/platform-browser';
import { Observable } from 'rxjs';

@Component({
  selector: 'pxt-app',
  templateUrl: './pxt-app.component.html',
  styleUrls: ['./pxt-app.component.scss']

})
export class PxtAppComponent {

  //Properties
  routes: any[] = [];
  groups: any[] = [];
  menus: any[] = [];
  system: String = "SYSTEM NAME"
  urlImg: string = 'http://imagensdsv.peixoto.com.br/auth/mini_logo.png';
  menuSelected = "";
  usuerLogged = "Loogged user";
  menusHtml: SafeHtml;
  result: Observable<boolean>;
  _mobileQueryListener: () => void;
  mobileQuery: MediaQueryList;
  shouldRun = true;
  @Input() matMenu: MatMenu;
  @ViewChild('menus', { read: ViewContainerRef }) subContainer1: ViewContainerRef;
  @ViewChild('contextMenuTrigger', { read: MatMenuTrigger }) contextMenuTrigger: MatMenuTrigger;
  currentAdIndex = -1;
  @ViewChild(PxtContentBody) adHost: PxtContentBody;
  interval: any;
  menusReceived : any[];
  
  //Constructor
  constructor(changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    public componentFactoryResolver: ComponentFactoryResolver,
    @Inject(PxtAppComponentService) public pxtAppComponentService
  ) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
    this.result = pxtAppComponentService.infoInitial.subscribe(infoInitial => {
      if (infoInitial != undefined) {
        this.usuerLogged = infoInitial.userLogged;
        this.system = infoInitial.system;
        this.menusReceived = infoInitial.sideBarMenus;
        this.menus = infoInitial.sideBarMenus;
        this.prepareMenu();
      }
    });
    this.subscribeComponent();
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
    clearInterval(this.interval);
  }

  // Include of components in the application body
  loadComponent(route: any, adHost) {
    this.menuSelected = route.menuText;
    let adItem = route.menuSource;
    let componentFactory = this.componentFactoryResolver.resolveComponentFactory(adItem.component);
    let viewContainerRef = adHost.viewContainerRef;
    viewContainerRef.clear();
    let componentRef = viewContainerRef.createComponent(componentFactory);
  }

  // Subscription to the service responsible for including components in the body of the application
  subscribeComponent() {
    this.pxtAppComponentService.loadComponentObservable.subscribe(componentObj => {
      var arrayAux = this.menusReceived.filter(x=>x.menuSource != undefined && x.menuSource.component === componentObj.component);
      console.log(arrayAux);
      if(arrayAux.length == 1){
        this.menuSelected = arrayAux[0].menuText;
      }
      let componentFactory = this.componentFactoryResolver.resolveComponentFactory(componentObj.component);
      let viewContainerRef = this.adHost.viewContainerRef;
      viewContainerRef.clear();
      let componentRef = viewContainerRef.createComponent(componentFactory);
      (<AdComponent>componentRef.instance).data = componentObj.data;
    });
  }

  // Responsible for call method "loadcomponents()" informing parameters
  selectItemMenu(nav) {
    this.loadComponent(nav, this.adHost);
  }

  // Method responsible for preparing application menus;
  prepareMenu() {
    let arrayAux: any[];
    arrayAux = this.menus.filter(x => x.menuType == "group" && x.menuParent == "");
    var arrayAuxGroup = this.menus.filter(x => x.menuType == "group" && x.menuParent !== "");
    var arrayAuxItem = this.menus.filter(x => x.menuType == "item" && x.menuParent !== "");

    //add itens in groups
    arrayAuxItem.forEach(item => {
      var arrayTmp = arrayAuxGroup.filter(x => x.menuId == item.menuParent);
      if (arrayTmp.length == 1) {
        if (arrayTmp[0].childs == undefined) {
          arrayTmp[0].childs = [];
        }
        arrayTmp[0].childs.push(item);
      };
    });
    //add groups in groups
    arrayAuxGroup.forEach(item => {
      var arrayTmp = arrayAuxGroup.filter(x => x.menuId == item.menuParent);
      if (arrayTmp.length == 1) {
        if (arrayTmp[0].childs == undefined) {
          arrayTmp[0].childs = [];
        }
        arrayTmp[0].childs.push(item)
      };
    });
    //add groups in super-groups
    arrayAuxGroup.forEach(item => {
      var arrayTmp = arrayAux.filter(x => x.menuId == item.menuParent);
      if (arrayTmp.length == 1) {
        if (arrayTmp[0].childs == undefined) {
          arrayTmp[0].childs = [];
          arrayTmp[0].childs.push(item);
        } else {
          arrayTmp[0].childs.push(item);
        }
      };
    });

    //add itens in super-groups
    arrayAuxItem.forEach(item => {
      var arrayTmp = arrayAux.filter(x => x.menuId == item.menuParent);
      if (arrayTmp.length == 1) {
        if (arrayTmp[0].childs == undefined) {
          arrayTmp[0].childs = [];
        }
        arrayTmp[0].childs.push(item);
      };
    });
    this.menus = arrayAux;
  }
}
