import { Component, OnInit, Input, ViewChild, Inject } from '@angular/core';
import { PxtAppComponentService } from '../../../services/pxt-app-components.service';


@Component({
  selector: 'pxt-app-menu-item',
  templateUrl: './pxt-app-menu-item.component.html',
  styleUrls: ['./pxt-app-menu-item.component.css']
})
export class PxtAppMenuItemComponent implements OnInit {

  @Input() items: any[];
  @ViewChild('childMenu') public childMenu;

  constructor(@Inject(PxtAppComponentService) public pxtAppComponentService) { }
  ngOnInit() {
  }

  loadComponent(child) {
    this.pxtAppComponentService.loadComponent({component: child.menuSource.component, data:""});
  }

}
