import { Injectable } from "@angular/core";
import { Observable, Subject } from 'rxjs';

@Injectable()
export class PxtAppComponentService {
    private submenusItens: Subject<any> = new Subject<any>();
    public readonly submenusItensObservable: Observable<any> = this.submenusItens.asObservable();

    private _loadComponent: Subject<any> = new Subject<any>();
    public readonly loadComponentObservable: Observable<any> = this._loadComponent.asObservable();

    private _setUserLogged: Subject<any> = new Subject<any>();
    public readonly userLoggedObservable: Observable<any> = this._setUserLogged.asObservable();

    private _setInfoInit: Subject<any> = new Subject<any>();
    public readonly infoInitial: Observable<any> = this._setInfoInit.asObservable();

    setSubmenus(routes: any) {
        this.submenusItens.next(routes);
    }

    setInitialInfo(infoInitial) {
        this._setInfoInit.next(infoInitial)
    }

    loadComponent(component: any) {
        this._loadComponent.next(component);
    }

    setUser(user: any) {
        this._setUserLogged.next(user);
    }

}
